  .pt-datetimepicker .pt-datepicker {
    border-bottom: 1px solid rgba(16, 22, 26, 0.15);
    padding: 0 0 10px; }
  .pt-datetimepicker .pt-timepicker {
    padding: 10px 0 0; }

